.main-post {
  // margin-bottom: 20px;
  @include clearfix();
  position: relative;
  padding-bottom: 66px;
  &:after {
    position: absolute;
    bottom: 0;
    display: block;
    left: 50%;
    @include getIcon("\79");
    font-size: 35px;
    margin-left: -17px;
  }
  h1, h2, h3, h4, h5, h6 {
    font-family: $robotoSlab;
  }
  & &_title {
    font-size: 1.37500rem
  }
  & &_subtitle {
    color: rgba(get-color(dark), .6);
    font-family: $roboto;
    font-size: 1.25rem;
    margin-bottom: 1.25rem;
  }
  &_date {
    color: rgba(get-color(dark), .6);
    font-size: 0.6875rem;
    display: inline-block;
    margin: 0 0 12px;
  }
  &_navigation {
    @include clearfix();
    display: block;
    padding-top: 22px;
    margin-left: -15px;
    margin-right: -15px;

    @include mq($from: desktop) {
      display: none;
    }
  }
  p {
    color: rgba(get-color(dark), .6);
    font-family: $roboto;
    line-height: 24px;
  }
  small {
    color: rgba(get-color(dark), .8);
    font-family: $roboto;
    font-size: 0.857rem;
  }
  blockquote {
    padding-left: 59px;
    margin: 40px 0;
    p {
      color: rgba(get-color(dark), .5);
      font-family: $roboto;
      font-style: italic;
    }
    span {
      color: rgba(get-color(dark), .8);

    }
  }

  &_social-share {
    padding: 20px 0;
    text-align: center;
    li {

      @include mq($until: mobile) {
        &:first-child {
          margin: 0;
          display: block;
        }
      }

      display: inline-block;
      margin: 0 16px 0 0;
      vertical-align: middle;
      p {
        color: rgba(get-color(dark), .4);
        font-style: italic;
        margin: 0;
      }
      a {
        color: get-color(gray);
        font-size: 22px;

      }
    }
  }

  &_social {
    text-align: left;
    padding: 20px 0;

    > div {
      float: right;
    }

    &:after {
      display: table;
      content: ' ';
      clear: both;
    }

    @include mq($from: tablet) {
      text-align: right;
    }

    .social-text {
      display: inline-block;
      width: 54%;

      p {
        color: rgba(get-color(dark), .4);
        font-style: italic;
        margin: 0;
        line-height: 1.6rem;
      }

      @include mq($from: tablet) {
        width: auto;
        margin-right: 16px;
      }
    }

    .social-links {
      display: inline-block;
      width: 46%;
      text-align: right;

      li {
        display: inline-block;
        margin: 0 8px 0 0;
        vertical-align: middle;

        a {
          color: get-color(gray);
          font-size: 22px;

        }

        @include mq($from: tablet) {
          margin: 0 16px 0 0;
        }
      }

      @include mq($from: tablet) {
        width: auto;
      }
    }
  }
}

.post-video {
  &_wrapper {
    position: relative;
    margin: 0 0 20px;
    &:after {
      background-color: rgba(get-color(bluedark), .4);
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      z-index: 1;
    }
  }
  &_play {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -68px;
    margin-top: -68px;
    z-index: 2;
  }
  &_text {
    color: rgba(get-color(bluedark), .4);
    font-size: 0.6875rem;
    margin: 0;
  }
}

.post-gallery {
  margin: 0 0 20px;
  &_item {
    display: block;
    margin: 0 0 30px;
    position: relative;
  }
  &_more {
    span {
      color: get-color(white);
      font-family: $robotoSlab;
      font-size: 2rem;
      z-index: 1;
      @include center();
    }
    &:after {
      content: "";
      background-color: rgba(get-color(bluedark), .5);
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
    }
  }
}

.post-audio {
  background-color: get-color(light);
  position: relative;
  height: 42px;
  padding: 10px 60px 10px 42px;
  margin: 0 0 20px;
  width: 90%;
  @include mq($from: tablet) {
    width: 96%;
  }
  @include mq($from: desktop) {
    width: 100%;
  }
  &.active {
    background-color: get-color(lightgray);
    border-radius: 6px;
    padding-top: 5px;
    padding-bottom: 5px;
    .audio_time {
      margin-top: 8px;
    }
  }
  &_button {
    position: absolute;
    top: 0;
    left: 0;
    width: 42px;
    height: 42px;
  }
  &_icon {
    @include center();
    font-size: 0.75rem;
  }
  &_title {
    margin: 0;
    overflow: hidden;
    text-overflow: ellipsis;
    height: 25px;
  }
  &_timeline {
    background-color: rgba(get-color(bluedark), .10);
    height: 5px;
    width: 107%;
  }
  &_time {
    position: absolute;
    top: 0;
    right: 0;
    color: rgba(get-color(dark), .6);
    // @include center(v);
    margin-right: 18px;
    margin-top: 10px;
  }
  &_download {
    position: absolute;
    left: 100%;
    top: 0;
    height: 42px;
    padding: 9px 15px;
  }
}