.schedule {
  display: table;
  table-layout: fixed;
  margin: 0 0 8px;
  width: 100%;
  // height: 115px;
  &_left {
    display: table-cell;
    vertical-align: top;
    width: 97px;
    padding: 10px 0;
  }
  &_right {
    display: table-cell;
    vertical-align: top;
    padding: 10px 15px;
  }
  &_time {
    padding-left: 31px;
    position: relative;
    &--has-dot {
      // padding: 18px 25px;
      &:before {
        content: "";
        @include circle(7px, get-color(red));
        margin-top: -3px;
        margin-left: 15px;
        position: absolute;
        left: 0;
        top: 50%;
        @include mq($from: tablet) {
          // margin-left: -15px;
        }
      }
    }
  }
  &_link {
    color: get-color(white);
    &:hover {
      color: rgba(get-color(white), .6);
    }
  }
}